.TodoItem {
  background-color: #FAFAFA;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  box-shadow: 0px 5px 4px rgba(143, 97, 250, 0.2);
  border-radius: 16px;
  transition: all .2s ease-in-out;
  background-color: #635985;
}

.TodoItem--complete {
  background-color: #443c68;
  transform: scale(0.98);
}

.TodoItem:hover {
  transform: scale(1.02);
  background-color: #635985;
  box-shadow: 0px 5px 8px rgba(143, 97, 250, 0.5);
}

.TodoItem-p {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
}
.TodoItem-p--complete {
  text-decoration: line-through;
}